function Navbar(props) {
    return (
        <nav className="bg-gray-100 px-2 py-1 w-full shadow-xl">
            <a href="/">
                <img 
                    src="data:image/png;base64," 
                    className="border-2 border-blue-800 rounded-full h-10 w-10"
                    alt="FunFace"
                />
            </a>
        </nav>
    );
}

export default Navbar;